import { Component, Input, AfterViewInit, forwardRef } from '@angular/core';
import { BcCarouselBehavior, BcCarouselConfig } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';
import { NG_VALUE_ACCESSOR } from '@angular/forms';


/**
 * Componente Carousel
 *
 * @export
 * @class BcCarouselBehavior
 * @implements {OnInit}
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'bc-carousel',
  templateUrl: 'bc-carousel.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BcCarouselBehavior),
      multi: true,
    },
  ],
})
export class BcCarouselComponent implements  AfterViewInit {
  /**
 * Esta entrada permite indicar un id único al componente dentro del DOM.
 *
 * @type {string}
 * @memberof BcCarouselComponent
 *
 */
  @Input() componentId: string = this.bcHelperService.getId(this);
  /**
   * Esta entrada de tipo BcCarouselComponent permite indicarle las opciones de configuración para construir el carousel.
   * Las opciones son: {
        iconNamePrev: string;
        iconNameNext: string;
        spacingBetweenItems?: 0 | 1 | 2 | 3 | 4 | 5;
        swipeSpeed: 1 | 2 | 3;
        items?: Array<BcCarouselItemsConfig>
        arrows: boolean;
        bullets: boolean;
        accessibility: boolean;
        initialSlide: number;
        slidesToShow: number;
        slidesToScroll: number;
        swipe: boolean;
        variableWidth: boolean;
        infinity: boolean;
        gridSystem: boolean;
        arrowsInBullets?: boolean;
        autoPlay?: boolean;
        sliderPagination?: boolean;
        autoplayTimeout?:number;
   * }
   * @type {BcCarouselComponent}
   * @memberof BcCarouselComponent
   */
  @Input() configCarouselInitial: BcCarouselConfig;

  /**
   * Esta entrada se utiliza para dar el posicionamiento de los botones .
   * Las opciones son: top | bottom
   *
   * @type {('top' | 'bottom')}
   * @memberof BcInputSelectComponent
   */
  @Input() positionArrows: 'top' | 'bottom' = 'bottom';

  /**
   * Esta entrada se utiliza para asignarle titulo al carousel,
   * para temas de accesbilidad.
   * @type {string}
   * @memberof BcInputSelectComponent
   */
  @Input() ariaLabelCarousel = '';

  /**
   * Comportamiento de componente
   *
   * @type {BcHeaderBehavior}
   * @memberof BcCarouselBehavior
   */
  BcCarouselBehavior: BcCarouselBehavior = new BcCarouselBehavior();

  constructor(private bcHelperService: BcHelperService) {
  }

  ngAfterViewInit(): void {
    this.BcCarouselBehavior.setId(this.componentId);
    this.BcCarouselBehavior.setOptions(this.configCarouselInitial);
    this.BcCarouselBehavior.setUp();
  }

}
